  methods: {
    loginUser() {
      const username = document.getElementById("username").value,
        country = document.getElementById("country").value,
        age = document.getElementById("age").value,
        isM = document.getElementById("male").checked,
        isF = document.getElementById("female").checked,
        isRemember = document.getElementById("remember").checked;
      let gender = "";

      // Basic Validations :

      if (username.length > 14 || username.length < 3) {
        alert(
          "the username must be a minimum of 3 characters and a maximum of 14"
        );
        return;
      }

      if (age > 60 || age < 18) {
        alert("we are sorry :'( , only users between 18 and 60 are allowed.");
        return;
      }

      if (!country) {
        alert("Choose your country before submitting please !");
        return;
      }

      if (isM == true && isF == false) {
        gender = "male";
      } else if (isF == true && isM == false) {
        gender = "female";
      } else {
        alert("Choose your gender before submitting please !");
        return;
      }

      // Storing user :

      const newUser = {
        name: username,
        age,
        details: {
          gender,
          country,
        },
      };

      this.$store.commit("setUser", newUser);

      if (isRemember) {
        localStorage.setItem("thisUser", JSON.stringify(newUser));
      }

      document.getElementById("settings-link").click();
    },
  },
  mounted() {
    // redirect to home if the user exists :

    if (localStorage.getItem("thisUser")) {
      document.getElementById("home-link").click();
